@font-face {
  src: url('./fonts/Inter-Regular.woff2') format('woff2');
  font-family: 'Inter';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url('./fonts/Inter-Black.woff2') format('woff2');
  font-family: 'Inter';
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

body {
  margin: 0 auto;
  font-family:
    'Inter',
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
}
